<template>
    <div class="popUp">

    <!---->
    <Category v-if="dig===0" v-on:on="on"></Category>

   

	 <!---->
      <BtnNav v-if="dig===1" v-on:on="on"></BtnNav>
       <!---->
      <Photos v-if="dig===2" v-on:on="on"></Photos>
        <!---->
	<articlelists  v-if="dig===3" v-on:on="on"></articlelists>
	 <!---->
    <announcement v-if="dig===4" v-on:on="on"></announcement>
	
	 <!---->
    <Shuffling v-show="dig===5" v-on:on="on"></Shuffling>

<!---->

<!---->
      <Product v-if="dig===6" v-on:on="on"></Product>
<!---->
        <!--<categories v-if="dig===7" v-on:on="on"></categories>-->
	 <!---->
        <ification v-if="dig===8" v-on:on="on"></ification>

  <!---->
<edit v-if="hide"></edit>
 <!---->
<Addarticle v-if="hide"></Addarticle>
 <!---->
<UEditor  v-if="hide"></UEditor>

<AtBottom  v-if="hide"></AtBottom>
    </div>
</template>

<script>
import Photos from './Photos'
import ification from './ification'
import categories from './categories'
import articlelists from './articlelists'
import Product from './Product'
import BtnNav from './BtnNav'
import Shuffling from './figure'
import Category from './Category'
import announcement from './announcement'

import edit from '../Editmodule/edit'
import Addarticle from '../Editmodule/Addarticle'
import AtBottom from '../Editmodule/AtBottom'
import UEditor from '../PublicModule/UEditor'
export default {
props:["dig"],
  data(){
    return{
      hide:false,
      sub:this.dig
    }
  },
  methods:{
     on:function(Index){
       
       this.$emit("hideLink",Index)
     },
    
    
  },
  components:{
 ification,
 categories,
 articlelists,
 Product,
 BtnNav,
 Photos,
 Shuffling,
 Category,
 announcement,
 edit,
 Addarticle,
 UEditor,
 AtBottom
  }
   
   
}
</script>

<style scoped>
   .popUp{
     position:absolute;
     top:0;
     left:0;
     width:100%;
     height:100%;
     background:rgba(0,0,0,0.3);
     z-index:999;
   width:100%;
     min-width:1400px;
    height:900px;
   }
</style>



