<template lang="html">
   
    <div class="addannouncement">
       <modelName :Textname="Textname" v-on:hideLink="hideLink" ></modelName>
       <div class="contentadd">
          <div class="top">
	  <span style="margin-right:6px"> : </span>
	   <Input v-model="value" placeholder="..." style="width: 170px"></Input>
	  </div>
	  <div class="clears">
	    <span style="margin-right:6px"> :</span>
	   <div class="radis">
	          <div>
		       <Radio-group v-model="animal">
        <Radio label=""></Radio>
        <Radio label=""></Radio>
      
    </Radio-group>
		  </div>
		  <div style="margin-top:20px" v-show="animal===animal1">
		    <Input v-model="connection" placeholder="" style="width: 90px"></Input>
	        <Button type="ghost" v-on:click="addLink()"></Button>
		  </div>
	   </div>
	  </div>
       </div>
       <div class="Button">
     <Button type="text" v-on:click.stop="submit"></Button>
       <Button type="text" v-on:click="hideLink"></Button>
  </div>
  
  <!---->
<div class="setlink" v-show=secondary>

 <articlelinks></articlelinks>

</div>
  
    </div>
   
</template>

<script>

import modelName from "../../PublicModule/modelName"
import NameModel from "../../PublicModule/NameModel"
import articlelinks from "../../PublicModule/articlelinks"
 export default {
 	props:["index"],
 data(){
     return {
       Textname:"",
       value:"",
    
       animal:"",
       animal1:"",
       connections:"",
       Json:{"name":"","url":""}
       }
   },
   methods:{

      hideLink:function(){
   
        this.$emit("hideLink",false)
      },
      //
      addLink:function(){
      
        if(this.secondary===false){
	      this.$store.dispatch("secondary",[true,2])
	   }else{
	      this.$store.dispatch("secondary",[false,2])
	   }
      },
      submit:function(){
        this.Json.name=this.value;
	  this.Json.url = this.connection;
	  if(this.index>=0){
	
	  	 this.$emit("amend",this.Json,this.index)
	  }else{
	 this.$emit("addEvent",this.Json)
	 }
	 this.hideLink();
      }
   },
     computed:{
    
      // 
     secondary(){
     	
        return this.$store.state.CateGory.secondary
      },
       // 
     connection(){
        return this.$store.state.CateGory.connection
       }

  },
   watch:{    // connectionjump
       connections:function(){
         
     
	    this.gather.url =  this.connections  //
       }
     },
   components:{
     modelName,
     NameModel,
     articlelinks
   }

 }
</script>

<style scoped>
	.setlink{
width:240px;
position:absolute;
top:260px;
right:60px;
}
.addannouncement{
 
}
.contentadd{
  padding:0 24px;
  font-size:14px;
}
.top,
.clears{
overflow:hidden;
margin-top:20px;
}
.clears > div,
.clears > span{
 float:left
}
.clears > div{
 width:70%;
}

 .Button{
 position:absolute;
 right:20px;
 bottom:10px;
}
</style>



