<template lang="html">
  <div class="pare">
        <modelName :Textname="Textname[0]" :_index=[false,2]></modelName>
        <div class="child">
          <span> : </span>
        <Select v-model="model4" size="small" style="width:120px;">
                   <Option v-for="(item,index) in article" :label="item.bigclassname" :value="item.bigclassname" :key="item.url"></Option>
          </Select></div>
        <div class="child"><span> : </span>
        <Select v-model="model5" size="small" style="width:120px;" >
                   <Option v-for="(item,index) in article" :label="item.bigclassname"  :value="item.bigclassname" :key="item.url"></Option>
                </Select>
      </div>
  <div class="Button">
       <Button type="text" v-on:click="addEvent"></Button>
         <Button type="text" v-on:click="secondary"></Button>
    </div>
        </div>
</template>

<script>
import modelName from "./modelName"
export default {
data(){
  return{
     articles:"",//
     Linktype:"",//
     Textname:[""],
     model4:"",
     model5:""
  }
},
  created(){
     this.$store.dispatch("article")   // 
},
computed: {
	 article:function(){
	 	
    return this.$store.state.CateGory.article  //
     }
},
methods:{

   secondary:function(){   //
       this.$store.dispatch("secondary",[false,2])
   },
   addEvent:function(){  //
       var Url = this.model4+""+this.model5 //value.url
       this.$store.dispatch("connection",Url)
       this.secondary()
    }
},
components:{
    modelName
  }


}

</script>

<style scoped>
.pare{
 position:relative;
 background:#fff;
 font-size:14px;
 width:100%;
padding-bottom:60px;
}
.child{
 padding:0 24px;
 margin-top:20px;
}
   .Button{
 position:absolute;
 right:20px;
 bottom:10px;
}

</style>



